<template>
  <div>
    <div>
      <h1>BYU Parking List</h1>
      <h2>Search and add a pin</h2>
      <div>
        <p><I>If you find a proper place that BYU students can park,
        please add the place.</I></p>
      </div>
      <label>
        <gmap-autocomplete
          @place_changed="setPlace">
        </gmap-autocomplete>
        <button @click="addMarker">Add</button>
      </label>
      <br/>

    </div>
    <br>
    <gmap-map
      :center="center"
      :zoom="14"
      style="width:100%;  height: 400px;"
    >
      <gmap-marker
        :key="index"
        v-for="(m, index) in markers"
        :position="m.position"
        @click="center=m.position"
      ></gmap-marker>
    </gmap-map>
  </div>
</template>

<script>
import json from '@/assets/marker.json'
export default {
  name: "GoogleMap",
  data() {
    return {
      // default to Montreal to keep it simple
      // change this to whatever makes sense
      center: { lat: 40.251531, lng: -111.649166 },
      markers: [],
      places: [],
      currentPlace: null,
      initMarkers: [],
    };
  },

  mounted() {
    this.importMarker();
    this.addMarkersToMap();
  },

  methods: {
    // receives a place object via the autocomplete component
    setPlace(place) {
      this.currentPlace = place;
    },
    addMarker() {
      if (this.currentPlace) {
        const marker = {
          lat: this.currentPlace.geometry.location.lat(),
          lng: this.currentPlace.geometry.location.lng()
        };
        this.markers.push({ position: marker });
        this.places.push(this.currentPlace);
        this.center = marker;
        this.currentPlace = null;
      }
    },
    importMarker() {
      this.initMarkers.push(json);
    },
    addMarkersToMap() {
      for (let i = 0; i < this.initMarkers[0].marker.length; i++) {
        const marker = {
          lat: this.initMarkers[0].marker[i].coordinate.latitude,
          lng: this.initMarkers[0].marker[i].coordinate.longitude,
        };

        this.markers.push({ position: marker });
      }
      
    },
  }
};
</script>